
#header {
  display: block;
  position: relative;
  background: #413E4A;
  padding: 50px 10px 0;
  font-size: 0;
  margin-bottom: 20px;
}
#header button.menu {
  position: absolute;
  z-index: 2;
  top: 10px;
  left: 10px;
  display: inline-block;
  width: 30px;
  height: 30px;
  overflow: hidden;
  text-indent: -10000px;
  border-top: 6px solid #F9F2E7;
  border-bottom: 6px solid #F9F2E7;
}
#header button.menu:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 6px;
  transform: translate(0, -50%);
  background: #F9F2E7;
}
#header nav {
  overflow: hidden;
  height: 0;
  transition: height 0.3s;
}
#header nav[show] {
  height: auto;
}
#header nav .sub a:last-child {
  border-bottom: 1px solid #F9F2E7;
}
#header nav .sub span {
  display: none;
}
#header nav a {
  display: block;
  color: #F9F2E7;
  border-bottom: 1px solid #F9F2E7;
  padding: 10px 0;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 5px;
  font-size: 16px;
  line-height: 20px;
}
#header nav a:hover {
  color: #F9F2E7;
}
#header nav a:last-child {
  border-bottom: none;
}
#header nav a.router-link-active {
  background: #00A0B0;
}
#header h3 {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  color: #F9F2E7;
  text-align: center;
  font-size: 30px;
  line-height: 50px;
  text-transform: uppercase;
}
#header h3 span {
  color: #EB6841;
}
@media (min-width: 1025px) {
#header {
    background: none;
    padding: 0;
}
#header nav {
    position: relative;
    z-index: 2;
}
#header nav .sub.active {
    background: #00A0B0;
}
#header h3 {
    position: relative;
    margin: 20px 0;
    color: #413E4A;
    font-size: 50px;
    letter-spacing: 2px;
}
#header nav {
    height: auto;
    padding: 0 10px;
    text-align: center;
    background: #413E4A;
    white-space: nowrap;
    overflow: visible;
}
#header nav .sub {
    position: relative;
}
#header nav .sub span {
    display: inline-block;
    cursor: default;
    padding-right: 20px;
    position: relative;
}
#header nav .sub span:after {
    content: '';
    position: absolute;
    right: 0;
    top: 3px;
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 10px solid #F9F2E7;
}
#header nav .sub .categoryLinks {
    background: #413E4A;
    position: absolute;
    top: 50px;
    left: 0;
    visibility: hidden;
}
#header nav .sub .categoryLinks a {
    display: block;
    text-align: left;
}
#header nav .sub:hover .categoryLinks {
    visibility: visible;
}
#header nav a, #header nav .sub {
    display: inline-block;
    border-bottom: none;
    padding: 15px;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #F9F2E7;
}
#header nav a:first-child, #header nav .sub:first-child {
    margin-top: 0;
}
#header nav a:hover, #header nav .sub:hover {
    color: #F9F2E7;
    background: #00A0B0;
}
}



