
#audio .inner .row {
  margin: 0 .9375rem;
  padding: .9375rem;
  background: #ffffff;
  border: 2px solid #00A0B0;
}
#audio .tabLinks {
  font-size: 0;
}
#audio .tab {
  display: inline-block;
  width: auto;
  padding: 15px;
  margin-right: 10px;
  background: #ffffff;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  background: #00A0B0;
  margin-bottom: 10px;
  border-right: 1px solid #ffffff;
}
#audio .tab:last-child {
  border-right: none;
}
#audio .tab.active {
  color: #ffffff;
  background: #CC333F;
}
#audio .song {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #00A0B0;
  border-bottom: 2px solid #00A0B0;
}
#audio .song:last-child {
  border-bottom: none;
}
#audio .song.active, #audio .song:hover {
  color: #ffffff;
  background: #CC333F;
}
#audio .list {
  margin-bottom: 0;
}
#audio .control {
  margin-bottom: 10px;
  font-size: 0;
}
#audio .control button, #audio .control div {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin: 0 10px;
}
#audio .control .prev {
  border-left: 3px solid #413E4A;
}
#audio .control .prev span {
  display: block;
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-right: 30px solid #413E4A;
}
#audio .control .play-pause {
  min-width: 30px;
}
#audio .control .play-pause .play {
  display: block;
  margin: 0 auto;
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 30px solid #413E4A;
}
#audio .control .play-pause .pause {
  display: none;
  margin: 0 auto;
  width: 15px;
  height: 40px;
  border-left: 5px solid #413E4A;
  border-right: 5px solid #413E4A;
}
#audio .control .play-pause.playing .play {
  display: none;
}
#audio .control .play-pause.playing .pause {
  display: block;
}
#audio .control .next {
  border-right: 3px solid #413E4A;
}
#audio .control .next span {
  display: block;
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 30px solid #413E4A;
}
#audio .control .total, #audio .control .decor, #audio .control .now {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
}
#audio .control .decor {
  margin: 0 5px;
}
#audio .control .now {
  margin-left: 10px;
}
#audio .control .total {
  margin-right: -10px;
  min-width: 40px;
}
#audio .control .line {
  position: relative;
  cursor: pointer;
  width: 200px;
  height: 40px;
}
#audio .control .line:before, #audio .control .line .current {
  content: '';
  z-index: 1;
  background: #413E4A;
  height: 10px;
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}
#audio .control .line .current {
  width: 0;
  margin: 0;
  background: #EB6841;
}
#audio .control .line .pointer {
  position: absolute;
  z-index: 1;
  width: 20px;
  height: 20px;
  left: 0;
  top: 0;
  margin: 0;
  background: #EB6841;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  left: 0;
}
#audio .control .volume .line {
  width: 100px;
}
#audio .control .volume .line .pointer {
  left: 70%;
}
#audio .control .volume .line .current {
  width: 70%;
}
#audio .control .speaker {
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px;
  cursor: pointer;
}
#audio .control .speaker.muted svg path {
  display: none;
}
#audio .control .speaker svg {
  width: 50px;
  height: 50px;
}
@media (min-width: 768px) {
#audio .tab {
    width: 25%;
    padding: 15px;
    margin: 0;
}
}



