
footer {
  display: block;
  padding: 20px;
  border: 1px solid black;
  margin: 20px 0.9375rem;
  background: #fff;
}
footer span {
  display: none;
}
footer a {
  line-height: 25px;
  display: block;
  text-align: left;
}
@media (min-width: 768px) {
footer a, footer span {
    display: inline;
}
}



