
#contact {
  margin-bottom: 0.9375rem;
  clear: both;
}
#contact .inner .row {
  margin: 0 0.9375rem;
  padding: 0.9375rem 0;
  background: #ffffff;
  border: 2px solid #413E4A;
}
#contact .columns div {
  margin-bottom: 10px;
}
#contact .columns h3 {
  font-weight: bold;
}
#contact .columns h3 span {
  color: #CC333F;
}
#contact .columns a {
  display: inline-block;
  margin-bottom: 10px;
  padding-left: 40px;
  position: relative;
}
#contact .columns a:before {
  content: '';
  left: 0;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  height: 32px;
  width: 32px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
#contact .columns a:hover:before {
  background-position: 0 100%;
}
#contact .columns a.email:before {
  background-image: url(~root/assets/img/pic_email.png);
}
#contact .columns a.facebook:before {
  background-image: url(~root/assets/img/pic_facebook.png);
}
#contact .columns a.youtube:before {
  background-image: url(~root/assets/img/pic_youtube.png);
}
#contact .columns:nth-child(2) div:first-child {
  font-size: 0;
}
#contact .columns:nth-child(2) div img {
  max-width: 50%;
}
@media (min-width: 768px) {
#contact .columns div {
    vertical-align: top;
    display: table-cell;
}
#contact .columns div:first-child {
    padding-right: 20px;
    font-size: 0;
}
#contact .columns div:first-child img {
    max-width: 50%;
}
#contact .columns div img {
    display: inline-block;
}
}



